* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.button-div {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
button {
  margin: 1rem 2rem 1rem 2rem;
}
h1 {
  display: flex;
  justify-content: center;
}
